<template>
     <div class="movie_body">
        <Loading v-if="isLoading" />
        <scroller v-else>
            <ul>
                <li  v-for="item in comingPlayList" :key="item.id">
                    <div class="pic_show"><img :src="item.img | setWH('128.180')" @tap="gotoDetail(item.id)"></div>
                    <div class="info_list">
                        <h2 @tap="gotoDetail(item.id)">{{item.nm}}</h2>
                        <p><span class="person">{{item.wish}}</span> 人想看</p>
                        <p>主演: {{item.star}}</p>
                        <p>{{item.rt}}</p>
                    </div>
                    <div class="btn_pre">
                        预售
                    </div>
                </li>
            </ul>
        </scroller>
    </div>
</template>
<script>
export default {
    name: "comingsoon",
    data: function(){
        return {
            comingPlayList:[],
            isLoading: true,
            preID: -1,
        }
    },
    methods: {
        gotoDetail: function(movieID){
            this.$router.push("/movie/detail/2/" + movieID)
        }
    },
    activated: function(){
        var _this = this;
        var cityID = this.$store.state.city.id;
        if(this.preID === cityID){return;}
        this.isLoading = true;
        this.$http.get("http://39.97.33.178/api/movieComingList?cityId=" + cityID).then(function(res){
            // console.log(res.data);
            if(res.data.status === 0){
                _this.comingPlayList = res.data.data.comingList;
                _this.isLoading = false;
                _this.preID = cityID;
            }
            else{
                console.error("获取失败");
            }
            
        })
    }
}
</script>
<style lang="scss" scoped>
    #content .movie_body{ flex:1; overflow:auto;}
    .movie_body ul{ margin:0 12px; overflow: hidden;}
    .movie_body ul li{ margin-top:12px; display: flex; align-items:center; border-bottom: 1px #e6e6e6 solid; padding-bottom: 10px;}
    .movie_body .pic_show{ width:64px; height: 90px;}
    .movie_body .pic_show img{ width:100%;}
    .movie_body .info_list { margin-left: 10px; flex:1; position: relative;}
    .movie_body .info_list h2{ font-size: 17px; line-height: 24px; width:150px; overflow: hidden; white-space: nowrap; text-overflow:ellipsis;}
    .movie_body .info_list p{ font-size: 13px; color:#666; line-height: 22px; width:200px; overflow: hidden; white-space: nowrap; text-overflow:ellipsis;}
    .movie_body .info_list .grade{ font-weight: 700; color: #faaf00; font-size: 15px;}
    .movie_body .info_list img{ width:50px; position: absolute; right:10px; top: 5px;}
    .movie_body .btn_mall , .movie_body .btn_pre{ width:47px; height:27px; line-height: 28px; text-align: center; background-color: #f03d37; color: #fff; border-radius: 4px; font-size: 12px; cursor: pointer;}
    .movie_body .btn_pre{ background-color: #3c9fe6;}
</style>
